html, body {
    background: #FFFFFF;
    text-align: center;
    height: 100%;
    overflow: hidden;
}
.svg-wrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    width: 320px;  
}
.shape {
    stroke-dasharray: 140 540;
    stroke-dashoffset: -474;
    stroke-width: 8px;
    fill: transparent;
    stroke: #ef9a9a;
    border-bottom: 5px solid black;
    transition: stroke-width 1s, stroke-dashoffset 1s, stroke-dasharray 1s;
    z-index: -1;
}
.text {
    font-family: 'Rajdhani', sans-serif;
    font-size: 22px;
    line-height: 32px;
    letter-spacing: 8px;
    color: #000000;
    top: -48px;
    position: relative;
    z-index: 100;
    border: none;
    fill: transparent;
    cursor: crosshair;
    outline: none;
    -webkit-appearance: none !important;
    -moz-appearance: none !important;
    appearance: none !important;
}
.svg-wrapper:hover .shape {
    stroke-width: 2px;
    stroke-dashoffset: 0;
    stroke-dasharray: 760;
}
.App {
  width: 100%;
  height: 100%;
}

.grid-container {
  display: grid;
  grid-template-columns: 0.1fr 0.1fr;
  grid-column-gap: 1vw;
  grid-row-gap: 1vw;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  -webkit-animation: fadein 1.5s;
          animation: fadein 1.5s;
}
.square-box {
  min-width: 100px;
  width: 15vw;
  height: 15vw;
  padding: 8px;
  font-size: 17px;
  line-height: 24px;
  border: 0.1px solid #515151;
  border-width: thin;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  /* box-sizing: border-box; */
}

.square-title {
  margin: auto;
  font-family: 'Rajdhani', sans-serif;
  font-size: larger;
  /* font-family: 'Abel', sans-serif; */
}

@-webkit-keyframes fadein {
  from {opacity: 0;}
  to {opacity: 1;}
}

@keyframes fadein {
  from {opacity: 0;}
  to {opacity: 1;}
}

